    width: 100% !important;
    height: 8px !important;
    background: -webkit-gradient(linear, left top, right top, from(#905CFF), color-stop(100.53%, #6126DF)) !important;
    background: linear-gradient(90deg, #905CFF 0%, #6126DF 100.53%) !important;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.2) !important;
    border-radius: 8px !important;
}
.ant-carousel .slick-dots li button{
    width: 8px !important;
    height: 8px !important;
    border-radius: 50% !important;
}
.ant-carousel .slick-dots li.slick-active{
